:root {
  --background: #ffffff;
  --foreground: #171717;
  --border-radius: var(--mui-shape-borderRadius);
  --border-color: var(--mui-palette-divider);
  --primary-color: var(--mui-palette-primary-main);
  --background-color: var(--mui-palette-background-default);
  --background-color-rgb: var(--mui-palette-background-paperChannel);
  --header-height: 64px;
  --header-z-index: var(--mui-zIndex-appBar);
  --footer-z-index: 10;
  --customizer-z-index: var(--mui-zIndex-drawer);
  --search-z-index: var(--mui-zIndex-tooltip);
  --drawer-z-index: var(--mui-zIndex-drawer);
  --backdrop-color: rgb(var(--mui-mainColorChannels-light) / 0.5);
  --linear-Gradient: linear-gradient(
    90deg,
    #00517b -8.5%,
    var(--primary-color-theme) 111.51%
  );
  --light-background: #f9fdff;
  margin: 0;
  padding: 0;

  /* New Variable for theme */
  --global-font-family: "Public Sans", serif;
  --primary-color-theme: #133e87;
  --secondary-color-theme: #252525;
  --white-color: #ffffff;
  --error-color: #fb3131;
  --golden-color: #ffc107;
  --light-text-color: #25252580;
  --light-border: #25252526;
  --divider-color: #9e9e9e26;
  --icon-color: #25252580;
  --outline-btn-hover-bg: #007dac1a;
  --outline-btn-hover-border: #007dac80;
  --btn-box-shadow: 2px 4px 4px -2px #00000033;
  --card-box-shadow: 0px 2px 10px 0px #4c4e6438;
  --auth-page-gradient: linear-gradient(194deg, #b6ebff 44%, #b6ebff 91%);
  --sidebar-gradient: linear-gradient(
    194.29deg,
    rgba(182, 235, 255, 0.08) 44.04%,
    rgba(182, 235, 255, 0.23) 91.11%
  );
  --sidebar-active-bg: #def6ff;
  --header-gradient: linear-gradient(
    136.18deg,
    #f8fdff 21.54%,
    #ecfaff 50.39%,
