@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}

body {
  color: var(--secondary-color-theme);
  background: var(--white-color);
  font-family: var(--global-font-family);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
label,
a {
  font-family: var(--global-font-family) !important;
}

p {
  margin: 0px;
}

/* Sign-Up Css */

.logo_alignment_data {
  display: flex;
  justify-content: center;
}

.side_img {
  max-width: 100%;
  height: auto;
}

.login_partition_data {
  display: grid;
  grid-template-columns: 2.3fr 1fr;
  height: 100vh;
  width: auto;
}

.data_size {
  width: 100%;
  max-width: 340px;
  padding: 50px 0px;
  margin: 0 auto;
}

.login_title_text {
  font-size: 24px;
  font-weight: 600;
  line-height: 28.2px;
  color: var(--primary-color-theme);
  margin-bottom: 24px;
  text-align: center;
}

.auth_left_data {
  background-image: url(../../public/Images/authpage_sideimg.png);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.auth_right_data {
  background: var(--sidebar-gradient);
  padding: 50px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  box-shadow: 1px 4px 29.4px 0px #00000024;
  overflow-y: auto;
}

.description_login_page {
  font-size: 12px;
  font-weight: 400;
  line-height: 14.1px;
  color: var(--secondary-color-theme);
  margin: 0px;
}

.description_login_page a {
  font-size: 12px;
  font-weight: 400;
  line-height: 14.1px;
  text-decoration-line: underline;
  text-decoration-style: solid;
  color: var(--primary-color-theme);
}

.description_login_main {
  display: flex;
  /* align-items: center; */
  gap: 5px;
  margin-bottom: 32px;
  flex-direction: column;
}

