.timeline-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
  margin-bottom: 20px;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}
.comment-timeline-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
  margin-bottom: 20px;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
  /* border: 1px solid #e3e7ef;
  border-radius: 4px; */
  /* padding: 10px; */
}

.timeline-item:hover {
  background: #e3f2fd;
  /* Light blue on hover */
}

.timeline-icon {
  width: 30px;
  height: 30px;
  background-color: #4caf50;
  color: white;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background 0.3s ease-in-out;
}

.timeline_card_header {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.timeline_card_info {
